.footer {
  height: 5rem;
  min-height: 5rem;
  max-height: 5rem;
  width: 100%;
  display: flex;
  gap: 2vw;
  border-top: solid 1px #888;
}

.toggleRag {
  display: flex;
  justify-content: center;
  align-items: center;
}

.toggleLabel {
  margin-left: 1rem;
}

.message {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.send {
  display: flex;
  justify-content: center;
  align-items: center;
}
